import {BrowserRouter as BR, Switch, Route, Link as A,useHistory} from 'react-router-dom';
import {useState} from 'react' ;

const Form=()=>{const history=useHistory(); const [kk,jj]=useState({something:"",hashed:""}); const sub=async (s)=>{s.preventDefault(); const fm=await fetch('/store',{method:"POST",headers:{'Content-Type':'application/json'},body:JSON.stringify({something:kk.something,hashed:kk.hashed})}); const ggfm=await fm.json(); if(ggfm.ok){alert('data saved');history.push('/');}else{alert('data not saved..error.');}};return(<form autoComplete="off" method="POST" onSubmit={sub}><input type="text" placeholder="something" value={kk.something} onChange={(s)=>jj({...kk,something:s.target.value})}/><input type="text" placeholder="hashed" value={kk.hashed} onChange={(s)=>jj({...kk,hashed:s.target.value})}/><input type="submit" value="save" /></form>);};
const Index=()=>{return(<A to="/store">store something</A>);};


const App=()=>{return(<div><h1>on every page</h1><Switch><Route path="/" exact component={Index} /><Route path="/store" exact component={Form} /></Switch></div>);};

export default App ;